<template>
  <div class=" text-center loginscreen  animated fadeInDown modify">
    <div class="row mt">
      <h1>页面设置</h1>
      <form class="form-horizontal middle-box">
        <div class="form-group">
          <label class="control-label col-sm-4">网站名称</label>
          <div class="col-sm-8">
            <input type="text" class="form-control" required="" v-model="form.web_name">
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-4">联系方式</label>
          <div class="col-sm-8">
            <input type="text" class="form-control" required="" v-model="form.web_contact">
          </div>
        </div>

        <div class="form-group">
          <label class="control-label col-sm-4">网站底部</label>
          <div class="col-sm-8">
            <input type="text" class="form-control" required="" v-model="form.web_bottom">
          </div>
        </div>

        <div class="form-group">
          <label class="control-label col-sm-4">使用手册</label>
          <div class="col-sm-8">
            <button type="button" class="btn btn-sm btn-primary file-select"><i class="fa fa-upload"></i>&nbsp;&nbsp;
              <span class="bold">选择文件</span>
            </button>
            <input id="file" type="file" class="file" @change="fileChange()">

            <p class="file-url">{{ file.name ? file.name : form['manual'] ? api_url + form['manual'] : '' }}</p>
          </div>
        </div>

        <hr>

        <div class="form-group">
          <label class="control-label col-sm-4">系统关闭原因</label>
          <div class="col-sm-8">
            <input type="text" class="form-control" required="" v-model="form.reason_for_closure">
          </div>
        </div>

        <div class="form-group">
          <label class="control-label col-sm-4">是否关闭系统</label>
          <div class="col-sm-8">
            <select class="form-control" v-model="form.shut_down">
              <option disabled value="">请选择</option>
              <option value="0">开启系统</option>
              <option value="1">关闭系统</option>
            </select>
          </div>
        </div>

        <div class="form-group">
          <div class="col-sm-offset-4 col-sm-8">
            <b class="btn btn-primary block full-width" @click="confirm()">确定</b>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import { API_URL } from '@/assets/config'
import dataService from '@/assets/method/data'
import Service from '@/assets/service/service'
export default {
  name: 'pageSet',
  data () {
    return {
      api_url: API_URL,
      form: {},
      file: ''
    }
  },
  methods: {
    confirm () {
      if (!this.form.id) {
        toastr.error('系统参数错误')
        return
      }
      if (this.file.name) {
        this.form['manual'] = dataService.fileUpload(this.file, true) ? dataService.fileUpload(this.file, true) : ''
      }
      Service.PageSet.edit(this.form.id, this.form).then(response => {
        if (response.code == 200) {
          toastr.success('修改成功,自动刷新页面')
          setTimeout(() => window.parent.location.reload(), 1000)
        }
      })
    },
    fileChange () {
      this.file = $('#file').get(0).files[0]
    }
  },
  created () {
    $('#file').parents('form').attr('enctype', 'multipart/form-data');
    Service.PageSet.get().then(response => this.form = response.data)
  }
}
</script>

<style scoped>
  .mt{
    margin: 100px 0 0;
  }
  .mt h1{
    font-weight: bold;
  }
  .modify {
    position: relative;
  }
  .file-select {
    float: left;
  }
  .file-url {
    display: inline-block;
    width: 100%;
    text-align: left;
  }
</style>



